@import "flex";
$textAlign: left, right, center, justify;
$positions: static, fixed, absolute, relative, sticky;

@each $position in $positions {
  .#{$position} {
    position: #{$position};
  }
}
ol,
ul {
  &.no-bullets {
    list-style-type: none;
  }
}
.full-width {
  width: 100%;
}
.align-right {
  margin-left: auto;
}
.align-left {
  margin-right: auto;
}
.align-center {
  margin-left: auto;
  margin-right: auto;
}
.overflow-auto {
  overflow: auto;
}
.overflow-hidden {
  overflow: hidden;
}
.overflow-clip {
  overflow: clip;
}
.overflow-visible {
  overflow: visible;
}
.overflow-scroll {
  overflow: scroll;
}
.overflow-x-auto {
  overflow-x: auto;
}
.overflow-y-auto {
  overflow-y: auto;
}
.overflow-x-hidden {
  overflow-x: hidden;
}
.overflow-y-hidden {
  overflow-y: hidden;
}
.overflow-x-clip {
  overflow-x: clip;
}
.overflow-y-clip {
  overflow-y: clip;
}
.overflow-x-visible {
  overflow-x: visible;
}
.overflow-y-visible {
  overflow-y: visible;
}
.overflow-x-scroll {
  overflow-x: scroll;
}
.overflow-y-scroll {
  overflow-y: scroll;
}
.visually-hidden {
  visibility: hidden;
}
.span-viewport {
  width: 100vw;
  position: relative;
  margin-left: -50vw;
  left: 50%;
}
.sr-only:not(:focus):not(:active) {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
