.simple-stepper {
  .question-and-answer {
    .question {
      font-size: 1.8rem;
      line-height: 2.8rem;
    }
    .stepper-description p:first-of-type {
      margin-top: 0.6rem;
    }
  }
}

.question-number {
  .number-container {
    background: #fff;
    padding: 8px 0 8px 0;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 4.5rem;
  }
}
.number {
  display: block !important;
  z-index: 100 !important;
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 50%;
  color: #fff;
  background: $sweetgrass;
  font-size: 20px;
  font-weight: bold;
}
.input-row {
  display: flex;
  flex-direction: row;
  &.answered {
    .question-number {
      position: relative;
      display: flex;
      flex-direction: column;
      &:after {
        content: "";
        height: 100%;
        background: linear-gradient(#bbb, #bbb) no-repeat center/2px 100%;
      }
    }
    &:before {
      background: none;
    }
  }
  [class*="stepper-button-row"] {
    width: 100%;
    .start-over {
      padding-left: 0;
    }
    @media (max-width: $screen--M) {
      align-items: flex-start;
      .start-over-container {
        border-top: 2px solid $grey2;
        width: 100%;
        .start-over {
          padding-top: 1.5rem;
          padding-bottom: 2.3rem;
          padding-left: 0;
        }
      }
      .print-btn {
        width: 100%;
        margin-left: 0;
        padding-top: 1rem;
        padding-bottom: 1rem;
        @media (max-width: $screen--M) {
          .icon.print {
            margin-left: -1rem;
          }
        }
      }
    }
  }
}
